<template>
  <div class="home_div">
    <div id="container"></div>
      <div class="map_title">
        <el-switch
            v-model="value1"
            active-text="显示热力图"
            inactive-text=""
            @change="changValue()">
        </el-switch>
      </div>
  </div>

</template>


<script>
import AMapLoader from '@amap/amap-jsapi-loader';

export default {
  data() {
    return {
      marker: null,
      value1: true,
      map: null,
      heatMap: null,
      markers: [],
      //假数据   经纬度
      lnglats: [
        [118.3093, 32.3153],
        [118.3244, 32.2786],
        [118.3118, 32.3038],
      ],
      data: [{
        "lng": 118.3093,
        "lat": 32.3153,  //滁州市老年大学监测站
        "count": 41
      },
        {
          "lng": 118.3244,
          "lat": 32.2786,  //滁州市人大宾馆
          "count": 79
        },
        {
          "lng": 118.3118,
          "lat": 32.3038,  //滁州市市政府原办公楼"
          "count": 89
        },
        {
          "lng": 110.35,
          "lat": 20.02,  //滁州市市政府原办公楼"
          "count": 100
        },
        {
          "lng": 110.33,
          "lat": 19.98,  //滁州市市政府原办公楼"
          "count": 99
        },
        {
          "lng": 110.72,
          "lat": 19.61,  //滁州市市政府原办公楼"
          "count": 98
        },
        {
          "lng": 110.31,
          "lat": 19.68,  //滁州市市政府原办公楼"
          "count": 90
        },
        {
          "lng": 110.324088,
          "lat":20.033974 ,//海口
          "count": 20
        },
        {
          "lng":110.006008,
          "lat":19.740726 ,//澄迈
          "count": 40
        },
        {
          "lng":110.354133,
          "lat":19.682566 ,//定安
          "count": 30
        },
        {
          "lng":110.46949,
          "lat":19.260965,//琼海
          "count": 60
        },
        {
          "lng":110.793586,
          "lat":19.543863 ,//文昌
          "count": 50
        },
        {
          "lng":109.687812,
          "lat":19.913553,//临高
          "count": 30
        },
        {
          "lng":109.577949,
          "lat":19.521891 ,//儋州
          "count": 20
        },
        {
          "lng":109.047859,
          "lat":19.302164 ,//昌江
          "count": 60
        },
        {
          "lng":108.650978,
          "lat":19.098368 ,//东方
          "count": 20
        },
        {
          "lng":109.167161,
          "lat":109.167161,//乐东
          "count": 10
        },
        {
          "lng":109.167161,
          "lat":18.775798 ,//五指山
          "count": 30
        },
        {
          "lng":109.694505,
          "lat":18.643962,//保亭
          "count": 50
        },
        {
          "lng":110.032335,
          "lat":18.506633 ,//陵水
          "count": 40
        },
        {
          "lng":109.829088,
          "lat":19.033977 ,//琼中
          "count": 60
        },
        {
          "lng":109.517076,
          "lat":18.240215 ,//三亚
          "count": 70
        },

        ]
    }
  },
  mounted() {
    //DOM初始化完成进行地图初始化
    this.initMap();
  },
  methods: {
    changValue() {
      if (!this.value1) {
        let data = []
        this.heatMap.setDataSet({
          data: data,
          max: 100
        });
      } else {
        this.heatMap.setDataSet({
          data: this.data,
          max: 100
        });
      }
    },


    initMap() {
      AMapLoader.load({
        key: "0e5904b2829e2e25ffcdecc5685b2209",             // 申请好的Web端开发者Key，首次调用 load 时必填
        version: "2.0",      // 指定要加载的 JSAPI 的版本，缺省时默认为 1.4.15
        plugins: ['AMap.MoveAnimation', 'AMap.ToolBar', 'AMap.Scale', 'AMap.MapType', 'AMap.Geolocation', 'AMap.HawkEye', 'AMap.HeatMap'],       // 需要使用的的插件列表，如比例尺'AMap.Scale'等
      }).then((AMap) => {
        var _this = this;
        this.map = new AMap.Map("container", {  //设置地图容器id
          resizeEnable: true,
          //mapStyle: 'amap://styles/normal',
          viewMode: "3D",    //是否为3D地图模式
          zoom: 10,           //初始化地图级别
          center: [118.3093, 32.3153], //初始化地图中心点位置
          doubleClickZoom: true,
          // 是否允许鼠标拖拽
          dragEnable: true,
          keyboardEnable: false,
        });

        //添加marker标记
        function addMarker() {
          _this.map.clearMap();
          var marker = new AMap.Marker({
            map: _this.map,
            position: [118.3093, 32.3153]
          });
          //鼠标点击marker弹出自定义的信息窗体
          marker.on('click', function () {
            infoWindow.open(_this.map, marker.getPosition());
          });
        }

        addMarker();

        //实例化信息窗体
        var title = '站点1<span style="font-size:11px;color:#F00;">价格:318</span>',
            content = [];
        content.push("<img style='position: relative;left: 10px' src='http://tpc.googlesyndication.com/simgad/5843493769827749134'>");
        content.push("");
        content.push("天气质量:");
        content.push("CO指数:");
        content.push("SO2指数:");
        content.push("NO2指数:");
        content.push("CO2指数:");
        content.push("PM2.5指数:");
        content.push("PM10指数:");
        content.push("<a href='https://ditu.amap.com/detail/B000A8URXB?citycode=110105'>该站点详细信息</a>");


        var infoWindow = new AMap.InfoWindow({
          isCustom: true,  //使用自定义窗体
          content: createInfoWindow(title, content.join("<br/>")),
          offset: new AMap.Pixel(16, -45)
        });

        // var infoWindow = new AMap.InfoWindow({offset: new AMap.Pixel(0, -30)});
        // for (var i = 0, marker; i < this.lnglats.length; i++) {
        //   var marker = new AMap.Marker({
        //     position: this.lnglats[i], //不同标记点的经纬度
        //     map: this.map,
        //     content: createInfoWindow(title, content.join("<br/>")),
        //     offset: new AMap.Pixel(2, 2),  //设置点偏移量
        //     //icon:'//src/assets/login.png'
        //   });
        //   marker.content = '我是第' + (i + 1) + '个Marker';
        //   marker.on('click', markerClick);
        //   marker.emit('click', {target: marker});
        // }
        //构建自定义信息窗体
        function createInfoWindow(title, content) {
          var info = document.createElement("div");
          info.className = "custom-info input-card content-window-card";

          //可以通过下面的方式修改自定义窗体的宽高
          info.style.width = "200px";
          // 定义顶部标题
          var top = document.createElement("div");
          var titleD = document.createElement("div");
          var closeX = document.createElement("img");
          top.className = "info-top";
          titleD.innerHTML = title;
          closeX.src = "https://webapi.amap.com/images/close2.gif";
          closeX.onclick = closeInfoWindow;

          top.appendChild(titleD);
          top.appendChild(closeX);
          info.appendChild(top);

          // 定义中部内容
          var middle = document.createElement("div");
          middle.className = "info-middle";
          middle.style.backgroundColor = 'white';
          middle.innerHTML = content;
          info.appendChild(middle);

          // 定义底部内容
          var bottom = document.createElement("div");
          bottom.className = "info-bottom";
          bottom.style.position = 'relative';
          bottom.style.top = '0px';
          bottom.style.margin = '0 auto';
          var sharp = document.createElement("img");
          sharp.src = "https://webapi.amap.com/images/sharp.png";
          bottom.appendChild(sharp);
          info.appendChild(bottom);
          return info;
        }

        //关闭信息窗体
        function closeInfoWindow() {
          _this.map.clearInfoWindow();
        }

        // AMap.event.addListener(marker, "click", function () {  //监听点标记的点击事件
        //   infoWindow.open(_this.map, marker.getPosition()); //信息窗体打开
        // })


        function markerClick(e){
          infoWindow.setContent(e.target.content);
          infoWindow.open(_this.map, e.target.getPosition());
        }
        this.map.setFitView();

        // 缩放地图到合适的视野级别
        //this.map.setFitView([marker])


        this.heatMap = new AMap.HeatMap(this.map, {
          radius: 50, //给定半径
          opacity: [0, 1],
          gradient: {
            1: '#FF4C2F',
            0.8: '#FAA53F',
            0.6: '#FFF100',
            0.5: '#7DF675',
            0.4: '#5CE182',
            0.2: '#29CF6F',
          }
        });
        this.heatMap.setDataSet({
          data: this.data,
          max: 100
        });
        // 在图面添加工具条控件，工具条控件集成了缩放、平移、定位等功能按钮在内的组合控件
        this.map.addControl(new AMap.ToolBar());

        // 在图面添加比例尺控件，展示地图在当前层级和纬度下的比例尺
        this.map.addControl(new AMap.Scale());

        // 在图面添加鹰眼控件，在地图右下角显示地图的缩略图
        this.map.addControl(new AMap.HawkEye({isOpen: true}));


        // 在图面添加定位控件，用来获取和展示用户主机所在的经纬度位置
        this.map.addControl(new AMap.Geolocation());


      }).catch(e => {
        console.log(e);
      })
    },
  },
}
</script>

<style>
.content-window-card {
  position: relative;
  box-shadow: none;
  bottom: 0;
  left: 0;
  width: auto;
  padding: 0;
}

.content-window-card p {
  height: 2rem;
}

.custom-info {
  border: solid 1px silver;
}

div.info-top {
  position: relative;
  background: none repeat scroll 0 0 #F9F9F9;
  border-bottom: 1px solid #CCC;
  border-radius: 5px 5px 0 0;
}

div.info-top div {
  display: inline-block;
  color: #333333;
  font-size: 14px;
  font-weight: bold;
  line-height: 31px;
  padding: 0 10px;
}

div.info-top img {
  position: absolute;
  top: 10px;
  right: 10px;
  transition-duration: 0.25s;
}

div.info-top img:hover {
  box-shadow: 0px 0px 5px #000;
}

div.info-middle {
  font-size: 12px;
  padding: 10px 6px;
  line-height: 20px;
}

div.info-bottom {
  height: 0px;
  width: 100%;
  clear: both;
  text-align: center;
}

div.info-bottom img {
  position: relative;
  z-index: 104;
}

span {
  margin-left: 5px;
  font-size: 11px;
}

.info-middle img {
  float: left;
  margin-right: 6px;
}
.home_div{
  padding:0px;
  margin: 0px;
  width: 100%;
  height: 100%;
  position: relative;
}
#container{
  width: 100%;
  height: 100%;
  position: absolute;
}
.map_title{
  position:absolute;
  z-index: 1;
  width: 100%;
  height: 50px;

}
.amap-logo{
  display: none;
}
.amap-copyright {
  opacity:0;
}
</style>